import { Button } from '@material-ui/core';
import React, { useEffect } from 'react'
import { useRef } from 'react';
import { useState } from 'react';
import { Link, useLocation} from 'react-router-dom';
import styled from 'styled-components'
import logo from '../images/logo.png'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import HomeIcon from '@material-ui/icons/Home';
import InfoIcon from '@material-ui/icons/Info';
import LocalDiningIcon from '@material-ui/icons/LocalDining';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import MailIcon from '@material-ui/icons/Mail';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import PersonIcon from '@material-ui/icons/Person';
import CloseIcon from '@material-ui/icons/Close';
import Badge from '@material-ui/core/Badge';

export default function Navbar() {
    const [scrollNav, setScrollNav] = useState(false);
    const [page, setPage] = useState('/');
    const navRef = useRef('');
    const location = useLocation();
    const user = true;


    const changeNav = () => {
        if (window.scrollY < 100 && window.innerWidth > 900) {
            setScrollNav(true)
        } else {
            setScrollNav(false)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav);
        return () => {
            window.removeEventListener('scroll', changeNav);
        };
    }, []);

    useEffect(() => {
        switch(location.pathname) {
            case '/':
                setPage('/')
                break
            case '/despre':
                setPage('/despre')
                break
            case '/produse':
                setPage('/produse')
                break
            case '/cos':
                setPage('/cos')
                break
            case '/contact':
                setPage('/contact')
                break
            case '/inregistrare':
                setPage('/inregistrare')
                break
            default:
                setPage('')
        }
    }, [location])

    return (
        <NavContainer ref={navRef} onMouseEnter={() => setScrollNav(true)}  scrollNav={scrollNav}>
            <ExpandMoreIcon onClick={() => setScrollNav(true)}/>

            <CloseIcon onClick={() => setScrollNav(false)} />

            <Link to="/"><Logo src={logo} alt="" /></Link>

            <Ul>
                <Li><NavLink  className="active" to="">Acasă
                    <HomeIcon/>
                    </NavLink>
                </Li>
                <Li><NavLink to="">Despre
                    <InfoIcon/>
                    </NavLink>
                </Li>
                <Li><NavLink to="">Contact
                    <MailIcon/>
                    </NavLink>
                </Li>
                <Li><NavLink to="">Produse
                    <LocalDiningIcon/>
                    </NavLink>
                </Li>
                <Li><NavLink to="">Coș
                    <Badge badgeContent={4} color="primary">
                        <ShoppingCartIcon/>
                    </Badge>
                    </NavLink>
                </Li>
                <Li><NavLink to="">{user ? 'Cont' : 'Înregistrare'}
                    {user ? <PersonIcon/> : <PersonAddIcon/>}
                    </NavLink>
                </Li>
            </Ul>

            <Button to="/" variant="contained">{user ? 'Deconectare' : 'Autentificare'}</Button>
        </NavContainer>
    )
}







const NavContainer = styled.nav`
    background-color: ${({ scrollNav }) => (scrollNav ? 'whitesmoke' : '#00000094')};
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 69;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    width: ${({ scrollNav }) => (scrollNav ? '100%' : '100px')};
    height: ${({ scrollNav }) => (scrollNav ? '70px' : '30px')};
    padding: 0 20px;
    clip-path: ${({ scrollNav }) => (scrollNav ? 'polygon(0 0, 100% 0%, 100% 100%, 50% 100%, 0 100%)' : 'polygon(0 0, 100% 0%, 100% 0, 50% 100%, 0 0)')};
    transition: ${({scrollNav}) => (scrollNav ? 'clip-path 0.5s linear 0.5s, width 0.5s linear 0s, height 0.5s linear, background-color 0.5s linear' : 'clip-path 0.5s linear 0s, width 0.5s linear 0.5s,  height 0.5s linear, background-color 0.5s linear')};
    @media screen and (max-width: 900px) {
        height: ${({scrollNav}) => (scrollNav ? '100vh' : '30px')};
        flex-direction: column;
        justify-content: space-around;
    }
    >button {
        border-radius: 50px;
        width: 100px;
        height: 40px;
        font-size: 0.8rem;
        color: white;
        background-color: #3b3b3b;
        text-transform: capitalize;
        &:hover {
            transition: all 0.5s ease;
            background-color: #202020 !important;
        }
    }
    > ul, > a, > button {
        transform: ${({ scrollNav }) => (scrollNav ? 'translateY(0px)' : 'translateY(-100px)')};
        transition: transform 2s ease, opacity 1s linear;
        @media screen and (max-width: 900px) {
            opacity: ${({ scrollNav }) => (scrollNav ? 1 : 0)};
        }
    }
    >svg:first-of-type {
        position: absolute;
        color: ${({scrollNav}) => (scrollNav ? '#00000024' : '#000000')};
        left: 50%;
        padding: 10px;
        box-sizing: content-box;
        z-index: 2;
        bottom: -12px;
        transform:  translateX(-50%) ${({scrollNav}) => (scrollNav ? 'rotate(180deg)' : 'rotate(0deg)')} ;
        transition: all 1s ease;
    }
    >svg:last-of-type {
        position: absolute;
        color: #3b3b3b;
        font-size: 2rem;
        right: 0;
        top: 0;
        padding: 10px;
        box-sizing: content-box;
        display: none;
        @media  screen and (max-width: 900px) {
            display: ${({scrollNav}) => (scrollNav ? 'flex' : 'none')}
        }
    }
`
const Ul = styled.ul`
    padding: 0;
    pointer-events: none;
    display: flex;
    align-items: center;
    text-align: center;
    @media screen and (max-width: 900px) {
        flex-direction: column;
    }
`
const Li = styled.li`
    list-style: none;
    padding: 10px 0;
    overflow: hidden;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: #5a5a5a;
        bottom: 0px;
        left: -100%;
        z-index: 2;
        transition: .5s ease;
    }
    &:hover::after {
        left: 0;
    }
`
const NavLink = styled(Link)`
    pointer-events: all;
    padding: 8px 10px;
    text-decoration: none;
    color: ${({scrollNav}) => (scrollNav ? 'transparent' : '#3b3b3b')};
    &.active {
        border-bottom: 3px solid #5a5a5a;
    }
    >svg, >span svg {
        font-size: 1rem;
        vertical-align: middle;
        margin-bottom: 2px;
        margin-left: 2px;
    }
`
const Logo = styled.img`
    width: 100px;
    object-fit: contain;
`